@import "./glitch.css";

.Typewriter {
  text-align: center;
}
.buttonsNavigation {
  display: flex;
  justify-content: center;
  // margin-top: 20px;
  &__item {
    color: #141414;
    text-decoration: none;
  }
}
.mauseContainer {
  width: 100%;
  height: 30vh;
  position: relative;
  z-index: 3;
  opacity: 1;
  transition: 1s;
  &--open {
    opacity: 0 !important;
    pointer-events: none;
  }
  span {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    width: 15px;
    animation-name: move;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    transition: 1s;
    cursor: pointer;
    opacity: 1;
    svg {
      fill: #f26b38;
    }
  }
}

@keyframes move {
  50% {
    top: calc(20% + 10px);
  }
  100% {
    top: 20%;
  }
}
