.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  z-index: 3;
  &__coppy {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    font-size: 10px;

    @media (max-width: 700px) {
      display: none;
    }
  }
  &__item {
    width: 20px;
    fill: #363636;
    cursor: pointer;
    transition: 0.5s;
    margin: 10px;
    &:hover {
      fill: #f26b38;
    }
  }
}
