* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Mono";
  font-weight: 400;
}
body {
  // padding-bottom: 10vh;
}

a {
  cursor: pointer;
}

.App {
  overflow-x: hidden;
  opacity: 0;
  transition: 500ms;
  z-index: 1;
  min-height: 100vh;
  &--active {
    opacity: 1;
  }
  border: 10px solid #f26b38;
}
